<template>
    <form method="post" @submit.stop.prevent="postNotice">
        <div class ="outer">
            <div class="inner">
         <div class ="notice">
             <input type="checkbox" id="mentor" v-model="notice.studyType">
            <label for ="mentor">멘토 멘티</label>
             <input type="checkbox" id="autonomy" v-model="notice.studyType">
             <label for="autonomy">자율 학습</label>
            Writer :  <input type="text" name="writer" required v-model="notice.writer" placeholder="글쓴이">
            <br>
            <br>
            Title : <input type="text" name="title" required v-model="notice.title" placeholder="제목">
            <br>
            <br>
            Content
            <br>
            <textarea name="content" required v-model="notice.content" placeholder="내용"></textarea>
            <br>
            <button type ="submit" class="mybtn">작성</button>
         </div>
        </div>
    </div>
    </form>
</template>
<script>
    // import temp from '../../../backend/data/notice'
    export default {
        data(){
            return{
                notice:{
                    studyType: "",
                    maxMember:"",
                    writer: "",
                    title: "",
                    content: ""
                }
            }
        },
        methods:{
            async postNotice() {
                //this.notice.studyType = this.checked
                const res = await this.$store.dispatch('appendNotice', this.notice)
                if(res.success === false) alert(res.message)
                else this.$router.push("/notice")
            }

        }


    }

</script>
<style>
    .outer{
        display: table;
        width: 100%;
        height: 100%;
    }
    .inner{
        display: table-cell;
        vertical-align: middle;
        text-align: center;
    }
    .notice{
        width: 60%;
        height: 100px;
        margin: auto;
        text-align: center;

    }
    .mybtn{
        border-radius:10px;
        background-color: #ecd7d2 !important;
    }
</style>